<template>
  <li class="dl-timeline-item">
    <div class="dl-timeline-item__tail"></div>

    <div v-if="!$slots.dot"
      class="dl-timeline-item__node"
      :class="[
        `dl-timeline-item__node--${size || ''}`,
        `dl-timeline-item__node--${type || ''}`
      ]"
      :style="{
        backgroundColor: color
      }"
    >
      <i v-if="icon"
        class="dl-timeline-item__icon"
        :class="icon"
      ></i>
    </div>
    <div v-if="$slots.dot" class="dl-timeline-item__dot">
      <slot name="dot"></slot>
    </div>

    <div class="dl-timeline-item__wrapper">
      <div v-if="!hideTimestamp && placement === 'top'"
        class="dl-timeline-item__timestamp is-top">
        {{timestamp}}
      </div>

      <div class="dl-timeline-item__content">
        <slot></slot>
      </div>

      <div v-if="!hideTimestamp && placement === 'bottom'"
        class="dl-timeline-item__timestamp is-bottom">
        {{timestamp}}
      </div>
    </div>
  </li>
</template>

<script>
  export default {
    name: 'DtTimelineItem',

    inject: ['timeline'],

    props: {
      timestamp: String,

      hideTimestamp: {
        type: Boolean,
        default: false
      },

      placement: {
        type: String,
        default: 'bottom'
      },

      type: String,

      color: String,

      size: {
        type: String,
        default: 'normal'
      },

      icon: String
    }
  };
</script>



