                            <div class="source" slot="source"><template>
  <div class="block">
    <span class="demonstration">默认</span>
    <dt-date-picker v-model="value1" type="datetime" placeholder="选择日期时间">
    </dt-date-picker>
  </div>
  <div class="block">
    <span class="demonstration">带快捷选项</span>
    <dt-date-picker v-model="value2" type="datetime" placeholder="选择日期时间" align="right" :picker-options="pickerOptions">
    </dt-date-picker>
  </div>
  <div class="block">
    <span class="demonstration">设置默认时间</span>
    <dt-date-picker v-model="value3" type="datetime" placeholder="选择日期时间" default-time="12:00:00">
    </dt-date-picker>
  </div>
</template>


</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
