<template><section>
<h2>Button</h2>
<p>介绍Button的使用</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;dt-button type=\&quot;text\&quot; @click=\&quot;dialogVisible = true\&quot;&gt;点击打开 Dialog&lt;/dt-button&gt;\n\n&lt;dt-dialog title=\&quot;提示\&quot; :visible.sync=\&quot;dialogVisible\&quot; width=\&quot;30%\&quot; :before-close=\&quot;handleClose\&quot;&gt;\n  &lt;span&gt;这是一段信息&lt;/span&gt;\n  &lt;span slot=\&quot;footer\&quot; class=\&quot;dialog-footer\&quot;&gt;\n    &lt;dt-button @click=\&quot;dialogVisible = false\&quot;&gt;取 消&lt;/dt-button&gt;\n    &lt;dt-button type=\&quot;primary\&quot; @click=\&quot;dialogVisible = false\&quot;&gt;确 定&lt;/dt-button&gt;\n  &lt;/span&gt;\n&lt;/dt-dialog&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data() {\n      return {\n        dialogVisible: false\n      };\n    },\n    methods: {\n      handleClose(done) {\n        this.$confirm('确认关闭？')\n          .then(_ =&gt; {\n            done();\n          })\n          .catch(_ =&gt; {});\n      }\n    }\n  };\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><dt-button type="text" @click="dialogVisible = true">点击打开 Dialog</dt-button>

<dt-dialog title="提示" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
  <span>这是一段信息</span>
  <span slot="footer" class="dialog-footer">
    <dt-button @click="dialogVisible = false">取 消</dt-button>
    <dt-button type="primary" @click="dialogVisible = false">确 定</dt-button>
  </span>
</dt-dialog>


</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">dt-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"dialogVisible = true"</span>&gt;</span>点击打开 Dialog<span class="hljs-tag">&lt;/<span class="hljs-name">dt-button</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">dt-dialog</span>
  <span class="hljs-attr">title</span>=<span class="hljs-string">"提示"</span>
  <span class="hljs-attr">:visible.sync</span>=<span class="hljs-string">"dialogVisible"</span>
  <span class="hljs-attr">width</span>=<span class="hljs-string">"30%"</span>
  <span class="hljs-attr">:before-close</span>=<span class="hljs-string">"handleClose"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>这是一段信息<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"footer"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"dialog-footer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dt-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"dialogVisible = false"</span>&gt;</span>取 消<span class="hljs-tag">&lt;/<span class="hljs-name">dt-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dt-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"dialogVisible = false"</span>&gt;</span>确 定<span class="hljs-tag">&lt;/<span class="hljs-name">dt-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">dt-dialog</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">dialogVisible</span>: <span class="hljs-literal">false</span>
      };
    },
    <span class="hljs-attr">methods</span>: {
      handleClose(done) {
        <span class="hljs-keyword">this</span>.$confirm(<span class="hljs-string">'确认关闭？'</span>)
          .then(<span class="hljs-function"><span class="hljs-params">_</span> =&gt;</span> {
            done();
          })
          .catch(<span class="hljs-function"><span class="hljs-params">_</span> =&gt;</span> {});
      }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>

<script>
  export default {
    data() {
      return {
        dialogVisible: false
      };
    },
    methods: {
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }
    }
  };
</script>


