<template><section>
<h2>Upload 上传</h2>
<p>介绍Upload 上传的使用</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;dt-upload class=\&quot;upload-demo\&quot; action=\&quot;https://jsonplaceholder.typicode.com/posts/\&quot; :on-preview=\&quot;handlePreview\&quot; :on-remove=\&quot;handleRemove\&quot; :before-remove=\&quot;beforeRemove\&quot; multiple :limit=\&quot;3\&quot; :on-exceed=\&quot;handleExceed\&quot; :file-list=\&quot;fileList\&quot;&gt;\n    &lt;dt-button size=\&quot;small\&quot; type=\&quot;primary\&quot;&gt;点击上传&lt;/dt-button&gt;\n    &lt;div slot=\&quot;tip\&quot; class=\&quot;dt-upload__tip\&quot;&gt;只能上传jpg/png文件，且不超过500kb&lt;/div&gt;\n  &lt;/dt-upload&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data() {\n      return {\n        fileList: [{name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}, {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}]\n      };\n    },\n    methods: {\n      handleRemove(file, fileList) {\n        console.log(file, fileList);\n      },\n      handlePreview(file) {\n        console.log(file);\n      },\n      handleExceed(files, fileList) {\n        this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);\n      },\n      beforeRemove(file, fileList) {\n        return this.$confirm(`确定移除 ${ file.name }？`);\n      }\n    }\n  }\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <dt-upload class="upload-demo" action="https://jsonplaceholder.typicode.com/posts/" :on-preview="handlePreview" :on-remove="handleRemove" :before-remove="beforeRemove" multiple :limit="3" :on-exceed="handleExceed" :file-list="fileList">
    <dt-button size="small" type="primary">点击上传</dt-button>
    <div slot="tip" class="dt-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
  </dt-upload>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">dt-upload</span>
    <span class="hljs-attr">class</span>=<span class="hljs-string">"upload-demo"</span>
    <span class="hljs-attr">action</span>=<span class="hljs-string">"https://jsonplaceholder.typicode.com/posts/"</span>
    <span class="hljs-attr">:on-preview</span>=<span class="hljs-string">"handlePreview"</span>
    <span class="hljs-attr">:on-remove</span>=<span class="hljs-string">"handleRemove"</span>
    <span class="hljs-attr">:before-remove</span>=<span class="hljs-string">"beforeRemove"</span>
    <span class="hljs-attr">multiple</span>
    <span class="hljs-attr">:limit</span>=<span class="hljs-string">"3"</span>
    <span class="hljs-attr">:on-exceed</span>=<span class="hljs-string">"handleExceed"</span>
    <span class="hljs-attr">:file-list</span>=<span class="hljs-string">"fileList"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dt-button</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>点击上传<span class="hljs-tag">&lt;/<span class="hljs-name">dt-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"tip"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"dt-upload__tip"</span>&gt;</span>只能上传jpg/png文件，且不超过500kb<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">dt-upload</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">fileList</span>: [{<span class="hljs-attr">name</span>: <span class="hljs-string">'food.jpeg'</span>, <span class="hljs-attr">url</span>: <span class="hljs-string">'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'</span>}, {<span class="hljs-attr">name</span>: <span class="hljs-string">'food2.jpeg'</span>, <span class="hljs-attr">url</span>: <span class="hljs-string">'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'</span>}]
      };
    },
    <span class="hljs-attr">methods</span>: {
      handleRemove(file, fileList) {
        <span class="hljs-built_in">console</span>.log(file, fileList);
      },
      handlePreview(file) {
        <span class="hljs-built_in">console</span>.log(file);
      },
      handleExceed(files, fileList) {
        <span class="hljs-keyword">this</span>.$message.warning(<span class="hljs-string">`当前限制选择 3 个文件，本次选择了 <span class="hljs-subst">${files.length}</span> 个文件，共选择了 <span class="hljs-subst">${files.length + fileList.length}</span> 个文件`</span>);
      },
      beforeRemove(file, fileList) {
        <span class="hljs-keyword">return</span> <span class="hljs-keyword">this</span>.$confirm(<span class="hljs-string">`确定移除 <span class="hljs-subst">${ file.name }</span>？`</span>);
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>

<script>
  export default {
    data() {
      return {
        fileList: [{name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}, {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}]
      };
    },
    methods: {
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(`确定移除 ${ file.name }？`);
      }
    }
  }
</script>


