<template>
  <div class="info">
    <h4>Rectangle</h4>
    <ul>
      <li v-for="field in displayKeys" :key="field">
        <label>{{ field.toUpperCase() }}</label>
        <div :class="field">
          <input
            type="number" 
            :value="rect[field] | numberBeautify" 
            @change="onValueChange(field, $event)"
            @input="onValueChange(field, $event)"
          >
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'DtInfo',
  componentName: 'DtInfo',
  props:{
    rect: Object
  },

  computed: {
    displayKeys() {
      /* eslint-disable */
      const { animation, ...rest } = this.rect
      return Object.keys(rest)
    }
  },

  methods: {
    onValueChange(field, e) {
      let value = Number(e.target.value)
      if (field === 'r') {
        value = value > 180 ? 180 : (value < -180 ? -180 : value)
      } else {
        value = value < 0 ? 0 : value
      }
      this.$emit('transformed', {[field]: value})
      this.$nextTick(() => {
        this.$emit('record')
      })
    }
  }
}
</script>



