es equal to 30.&lt;/p&gt;\n          &lt;/dt-draggable-resizable&gt; --&gt;\n        &lt;/dt-main&gt;\n        &lt;dt-aside class=\&quot;demo-right_aside\&quot; width=\&quot;200px\&quot;&gt;\n          &lt;ul class=\&quot;demo-right-aside_ul\&quot;&gt;\n            &lt;li&gt;\n              &lt;p&gt;width:&lt;/p&gt; &lt;dt-input :value=\&quot;mainInfo.width\&quot;&gt;&lt;/dt-input&gt;\n            &lt;/li&gt;\n            &lt;li&gt;&lt;p&gt;height:&lt;/p&gt; &lt;dt-input :value=\&quot;mainInfo.height\&quot;&gt;&lt;/dt-input&gt;&lt;/li&gt;\n            &lt;li&gt;&lt;p&gt;Y:&lt;/p&gt; &lt;dt-input :value=\&quot;mainInfo.startY\&quot;&gt;&lt;/dt-input&gt;&lt;/li&gt;\n            &lt;li&gt;&lt;p&gt;X:&lt;/p&gt; &lt;dt-input :value=\&quot;mainInfo.startX\&quot;&gt;&lt;/dt-input&gt;&lt;/li&gt;\n          &lt;/ul&gt;\n        &lt;/dt-aside&gt;\n      &lt;/dt-container&gt;\n    &lt;/dt-container&gt;\n  &lt;/div&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      value: '测试测试'\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <div style="height:1000px">
    <dt-container>
      <dt-header class="demo-header">Header</dt-header>
      <dt-container class="demo-container">
        <dt-aside class="demo-aside" width="200px">Aside</dt-aside>
        <dt-main class="demo-main">
          <div class="wrapper">
            <dt-ruler :lang="lang" :thick="thick" :scale="scale" :width="width" :height="height" :start-x="startX" :start-y="startY" :shadow="shadow" :hor-line-arr="lines.h" :ver-line-arr="lines.v" :corner-active="true" @handleline="handleLine" @oncornerclick="handleCornerClick">
            </dt-ruler>
            <div ref="screensRef" id="screens" @wheel="handleWheel" @scroll="handleScroll">
                <div ref="containerRef" class="screen-container">
                    <div id="canvas" :style="canvasStyle">
                      <dt-draggable-resizable :parent="true" :max-width="100" :max-height="100" :grid="[10,20]">
                        <p>Component costrained in parent with</p>
                      </dt-draggable-resizable>
                    </div>
                </div>
            </div>
        </div>
          <!-- <dt-draggable-resizable :grid="[20,40]" :x="10" :y="20" :minHeight="30" :minWidth="30">
            <p>Grid 20x40 starting with a 10x20 offset and min height, min width values equal to 30.</p>
          </dt-draggable-resizable> -->
        </dt-main>
        <dt-aside class="demo-right_aside" width="200px">
          <ul class="demo-right-aside_ul">
            <li>
              <p>width:</p> <dt-input :value="mainInfo.width"></dt-input>
            </li>
            <li><p>height:</p> <dt-input :value="mainInfo.height"></dt-input></li>
            <li><p>Y:</p> <dt-input :value="mainInfo.startY"></dt-input></li>
            <li><p>X:</p> <dt-input :value="mainInfo.startX"></dt-input></li>
          </ul>
        </dt-aside>
      </dt-container>
    </dt-container>
  </div>
</template>

</div>
                            
