<style>
/* body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  overflow: hidden; }

body * {
  box-sizing: border-box;
  user-select: none; } */

.wrapper {
  background-color: #f5f5f5;
  position: relative;
  /* top: 100px; */
  /* left: 100px; */
  width: 100%;
  height: 100%;
  /* border: 1px solid #DADADC;  */
}

#screens {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: auto;
}
#screens::-webkit-scrollbar-track {
  background-color: #2c2c32;
}
#screens::-webkit-scrollbar {
  width: 10px;
  height:10px;
}
#screens::-webkit-scrollbar-thumb {
  background-color: #7c7e84;
  /* border-radius: 20%; */
}
.screen-container {
  position: absolute;
  background-color: #2c2c32;
  width: 5000px;
  height: 3000px; }

.scale-value {
  position: absolute;
  left: 0;
  bottom: 100%; }

.button {
  position: absolute;
  left: 100px;
  bottom: 100%; }

.button-ch {
  position: absolute;
  left: 200px;
  bottom: 100%; }
.button-en {
  position: absolute;
  left: 230px;
  bottom: 100%; }

#canvas {
  position: absolute;
  top: 80px;
  left: 50%;
  margin-left: -80px;
  width: 160px;
  height: 200px;
  background: lightblue;
  transform-origin: 50% 0; }
</style><style>
  .demo-header{
    background-color: #3d4251;
    color: #fff;
  }
  .demo-container{
    height: 100%;
  }
  .demo-aside{
    background-color: #313541;
    color: #fff;
  }
  .demo-main{
    background-color: #2c2c32;
    color: #fff;
    padding: 0;
  }
  .demo-right_aside{
    right: 0;
    padding: 10px;
    color: rgb(177 179 183);
    background-color: hsl(225deg 14% 22%);
  }
  .demo-right-aside_ul p{
    color: rgb(177 179 183);
  }
</style>
<script>
export default {
    data() {
        return {
            scale: 2, //658813476562495, //1,
            startX: 0,
            startY: 0,
            lines: {
                h: [100, 200],
                v: [100, 200]
            },
            width: 0,
            height: 0,
            thick: 20,
            lang: "zh-CN", // 中英文
            isShowRuler: true, // 显示标尺
            isShowReferLine: true, // 显示参考线
            mainInfo: {
              width: 500,
              height: 400,
              startX: 0,
              startY: 0
            }
        }
    },
    computed: {
        shadow() {
            return {
                x: 0,
                y: 0,
                width: this.mainInfo.width,
                height: this.mainInfo.height
            }
        },
        canvasStyle() {
            return {
                width: this.mainInfo.width,
                height: this.mainInfo.height,
                transform: `scale(${this.scale})` 
            }
        }
    },
    methods: {
        handleLine(lines) {
            this.lines = lines;
        },
        handleCornerClick() {
            return;
        },
        handleScroll() {
            const screensRect = document
                .querySelector("#screens")
                .getBoundingClientRect();
            const canvasRect = document
                .querySelector("#canvas")
                .getBoundingClientRect();

            // 标尺开始的刻度
            const startX = (screensRect.left + this.thick - canvasRect.left) / this.scale;
            const startY = (screensRect.top + this.thick - canvasRect.top) / this.scale;
            
            this.startX = startX;
            this.startY = startY;
        },
        // 控制缩放值
        handleWheel(e) {
            if (e.ctrlKey || e.metaKey) {
                e.preventDefault();
                const nextScale = parseFloat(
                    Math.max(0.2, this.scale - e.deltaY / 500).toFixed(2)
                );
                this.scale = nextScale;
            }
            this.$nextTick(() => {
                this.handleScroll();
            });
        },
    },
    mounted() {
      console.log(this.$refs.screensRef)
        // 滚动居中
      const screensRef = this.$refs.screensRef
      this.width = screensRef.style.width || screensRef.clientWidth || screensRef.offsetWidth || screensRef.scrollWidth
      this.height = screensRef.style.height || screensRef.clientHeight || screensRef.offsetHeight || screensRef.scrollHeight
      console.log(this.width,this.height)
      this.$refs.screensRef.scrollLeft =
      this.$refs.containerRef.getBoundingClientRect().width / 2 - 300; // 300 = #screens.width / 2
    }
}
</script>


