<template>
  <dt-popover
    v-bind="$attrs"
    v-model="visible"
    trigger="click"
  >
  <div class="dt-popconfirm">
    <p class="dt-popconfirm__main">
    <i
      v-if="!hideIcon"
      :class="icon"
      class="dt-popconfirm__icon"
      :style="{color: iconColor}"
    ></i>
      {{title}}
    </p>
    <div class="dt-popconfirm__action">
      <dt-button 
        size="mini" 
        :type="cancelButtonType" 
        @click="cancel"
      >
        {{ displayCancelButtonText }}
      </dt-button>
      <dt-button 
        size="mini" 
        :type="confirmButtonType" 
        @click="confirm"
      >
        {{ displayConfirmButtonText }}
      </dt-button>
    </div>
  </div>
  <slot name="reference" slot="reference"></slot>
</dt-popover>
</template>

<script>
import DtPopover from '../../popover';
import DtButton from '../../button';
import {t} from '../../../locale';

export default {
  name: 'DtPopconfirm',
  props: {
    title: {
      type: String
    },
    confirmButtonText: {
      type: String
    },
    cancelButtonText: {
      type: String
    },
    confirmButtonType: {
      type: String,
      default: 'primary'
    },
    cancelButtonType: {
      type: String,
      default: 'text'
    },
    icon: {
      type: String,
      default: 'dt-icon-question'
    },
    iconColor: {
      type: String,
      default: '#f90'
    },
    hideIcon: {
      type: Boolean,
      default: false
    }
  },
  components: {
    DtPopover,
    DtButton
  },
  data() {
    return {
      visible: false
    };
  },
  computed: {
    displayConfirmButtonText() {
      return this.confirmButtonText || t('el.popconfirm.confirmButtonText');
    },
    displayCancelButtonText() {
      return this.cancelButtonText || t('el.popconfirm.cancelButtonText');
    }
  },
  methods: {
    confirm() {
      this.visible = false;
      this.$emit('confirm');
    },
    cancel() {
      this.visible = false;
      this.$emit('cancel');
    }
  }
};
</script>



