<template>
  <aside class="dt-aside" :style="{ width }">
    <slot></slot>
  </aside>
</template>

<script>
  export default {
    name: 'dt-aside',

    componentName: 'DtAside',

    props: {
      width: {
        type: String,
        default: '300px'
      }
    }
  };
</script>



