<template><section>
<h2>Rate 评分</h2>
<p>介绍Rate 评分的使用</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;div class=\&quot;block\&quot;&gt;\n    &lt;span class=\&quot;demonstration\&quot;&gt;默认不区分颜色&lt;/span&gt;\n    &lt;dt-rate v-model=\&quot;value1\&quot;&gt;&lt;/dt-rate&gt;\n  &lt;/div&gt;\n  &lt;div class=\&quot;block\&quot;&gt;\n    &lt;span class=\&quot;demonstration\&quot;&gt;区分颜色&lt;/span&gt;\n    &lt;dt-rate v-model=\&quot;value2\&quot; :colors=\&quot;colors\&quot;&gt;\n    &lt;/dt-rate&gt;\n  &lt;/div&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data() {\n      return {\n        value1: null,\n        value2: null,\n        colors: ['#99A9BF', '#F7BA2A', '#FF9900']  // 等同于 { 2: '#99A9BF', 4: { value: '#F7BA2A', excluded: true }, 5: '#FF9900' }\n      }\n    }\n  }\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <div class="block">
    <span class="demonstration">默认不区分颜色</span>
    <dt-rate v-model="value1"></dt-rate>
  </div>
  <div class="block">
    <span class="demonstration">区分颜色</span>
    <dt-rate v-model="value2" :colors="colors">
    </dt-rate>
  </div>
</template>


</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"block"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demonstration"</span>&gt;</span>默认不区分颜色<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dt-rate</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">dt-rate</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"block"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demonstration"</span>&gt;</span>区分颜色<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dt-rate</span>
      <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span>
      <span class="hljs-attr">:colors</span>=<span class="hljs-string">"colors"</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">dt-rate</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">value1</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">value2</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">colors</span>: [<span class="hljs-string">'#99A9BF'</span>, <span class="hljs-string">'#F7BA2A'</span>, <span class="hljs-string">'#FF9900'</span>]  <span class="hljs-comment">// 等同于 { 2: '#99A9BF', 4: { value: '#F7BA2A', excluded: true }, 5: '#FF9900' }</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
</section></template>

<script>
  export default {
    data() {
      return {
        value1: null,
        value2: null,
        colors: ['#99A9BF', '#F7BA2A', '#FF9900']  // 等同于 { 2: '#99A9BF', 4: { value: '#F7BA2A', excluded: true }, 5: '#FF9900' }
      }
    }
  }
</script>


