<template>
  <div class="dt-page-header">
    <div class="dt-page-header__left" @click="$emit('back')">
      <i class="dt-icon-back"></i>
      <div class="dt-page-header__title">
        <slot name="title">{{ title }}</slot>
      </div>
    </div>
    <div class="dt-page-header__content">
      <slot name="content">{{ content }}</slot>
    </div>
  </div>
</template>

<script>
import { t } from '../../../locale';
export default {
  name: 'DtPageHeader',

  props: {
    title: {
      type: String,
      default() {
        return t('el.pageHeader.title');
      }
    },
    content: String
  }
};
</script>



