<template><section>
<h2>Transfer 穿梭框</h2>
<p>介绍Transfer 穿梭框的使用</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;dt-transfer v-model=\&quot;value\&quot; :data=\&quot;data\&quot;&gt;&lt;/dt-transfer&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data() {\n      const generateData = _ =&gt; {\n        const data = [];\n        for (let i = 1; i &lt;= 15; i++) {\n          data.push({\n            key: i,\n            label: `备选项 ${ i }`,\n            disabled: i % 4 === 0\n          });\n        }\n        return data;\n      };\n      return {\n        data: generateData(),\n        value: [1, 4]\n      };\n    }\n  };\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <dt-transfer v-model="value" :data="data"></dt-transfer>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">dt-transfer</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"data"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">dt-transfer</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">const</span> generateData = <span class="hljs-function"><span class="hljs-params">_</span> =&gt;</span> {
        <span class="hljs-keyword">const</span> data = [];
        <span class="hljs-keyword">for</span> (<span class="hljs-keyword">let</span> i = <span class="hljs-number">1</span>; i &lt;= <span class="hljs-number">15</span>; i++) {
          data.push({
            <span class="hljs-attr">key</span>: i,
            <span class="hljs-attr">label</span>: <span class="hljs-string">`备选项 <span class="hljs-subst">${ i }</span>`</span>,
            <span class="hljs-attr">disabled</span>: i % <span class="hljs-number">4</span> === <span class="hljs-number">0</span>
          });
        }
        <span class="hljs-keyword">return</span> data;
      };
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">data</span>: generateData(),
        <span class="hljs-attr">value</span>: [<span class="hljs-number">1</span>, <span class="hljs-number">4</span>]
      };
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>

<script>
  export default {
    data() {
      const generateData = _ => {
        const data = [];
        for (let i = 1; i <= 15; i++) {
          data.push({
            key: i,
            label: `备选项 ${ i }`,
            disabled: i % 4 === 0
          });
        }
        return data;
      };
      return {
        data: generateData(),
        value: [1, 4]
      };
    }
  };
</script>


