<template>
  <li
    class="dt-dropdown-menu__item"
    :class="{
      'is-disabled': disabled,
      'dt-dropdown-menu__item--divided': divided
    }"
    @click="handleClick"
    :aria-disabled="disabled"
    :tabindex="disabled ? null : -1"
  >
    <i :class="icon" v-if="icon"></i>
    <slot></slot>
  </li>
</template>
<script>
  import Emitter from '../../../mixins/emitter';

  export default {
    name: 'DtDropdownItem',

    mixins: [Emitter],

    props: {
      command: {},
      disabled: Boolean,
      divided: Boolean,
      icon: String
    },

    methods: {
      handleClick(e) {
        this.dispatch('DtDropdown', 'menu-item-click', [this.command, this]);
      }
    }
  };
</script>



