<template>
  <transition name="dt-zoom-in-top" @after-leave="doDestroy">
    <div
      v-show="showPopper"
      class="dt-autocomplete-suggestion dt-popper"
      :class="{ 'is-loading': !parent.hideLoading && parent.loading }"
      :style="{ width: dropdownWidth }"
      role="region">
      <dt-scrollbar
        tag="ul"
        wrap-class="dt-autocomplete-suggestion__wrap"
        view-class="dt-autocomplete-suggestion__list">
        <li v-if="!parent.hideLoading && parent.loading"><i class="dt-icon-loading"></i></li>
        <slot v-else>
        </slot>
      </dt-scrollbar>
    </div>
  </transition>
</template>
<script>
  import Popper from '../../../utils/vue-popper';
  import Emitter from '../../../mixins/emitter';
  import DtScrollbar from '../../scrollbar';

  export default {
    components: { DtScrollbar },
    mixins: [Popper, Emitter],

    componentName: 'DtAutocompleteSuggestions',

    data() {
      return {
        parent: this.$parent,
        dropdownWidth: ''
      };
    },

    props: {
      options: {
        default() {
          return {
            gpuAcceleration: false
          };
        }
      },
      id: String
    },

    methods: {
      select(item) {
        this.dispatch('DtAutocomplete', 'item-click', item);
      }
    },

    updated() {
      this.$nextTick(_ => {
        this.popperJS && this.updatePopper();
      });
    },

    mounted() {
      this.$parent.popperElm = this.popperElm = this.$el;
      this.referenceElm = this.$parent.$refs.input.$refs.input || this.$parent.$refs.input.$refs.textarea;
      this.referenceList = this.$el.querySelector('.dt-autocomplete-suggestion__list');
      this.referenceList.setAttribute('role', 'listbox');
      this.referenceList.setAttribute('id', this.id);
    },

    created() {
      this.$on('visible', (val, inputWidth) => {
        this.dropdownWidth = inputWidth + 'px';
        this.showPopper = val;
      });
    }
  };
</script>



